$color-primary: lightblue;
$color-secondary: darkred;

html {
  font-size: 62.5%;
}

h1 {
  font-size: 2.8rem;
  font-weight: 600;
}

h2 {
  font-size: 2.2rem;
  font-weight: 600;
}

.box {
  font-size: 1.8rem;
  background: $color-primary;
  margin: 2rem 1rem;
}

.btnBuy {
  margin-top: 1rem;
  border: 3px solid $color-secondary;
  background: $color-secondary;
  cursor: pointer;
  color: white;
}

.btnBuy:hover {
  background: $color-primary;
  color: black;
}

.findUs::after {
  content: " --->";
}

img {
  width: 4rem;
}

.grand {
  width: 160px;
  height: 160px;
  background: red;
  margin: 1rem 15rem;
}

.parent {
  width: 120px;
  height: 120px;
  background: green;
}

.child {
  width: 80px;
  height: 80px;
  background: black;
}
